import React from 'react';


import { ModalCard, Link, ModalRoot, ModalPage, ModalPageHeader, Caption, Title, View, Epic, Tabbar, TabbarItem, ConfigProvider, Panel, PanelHeader, Group, Div, Gallery, Card, Placeholder, FormLayout, FormStatus, CardGrid, Header, Separator, Cell, Button, FixedLayout } from '@vkontakte/vkui';


import '@vkontakte/vkui/dist/vkui.css';
import Icon28UserOutline from '@vkontakte/icons/dist/28/user_outline';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
import mainmenu from './casino/casinobtn.png';
import dwlogo from './casino/darkweblogo.png';
import qiwilogo from './casino/qiwilogo.png';
import maindw from './casino/maindw.png';
import coinmain from './casino/coinmain.png';
import game from './casino/game.png';
import qiwi from './casino/qiwi.png';

import './css/dwbtn.css';
import './css/qiwibtn.css';

import bridge from '@vkontakte/vk-bridge'

bridge.send('VKWebAppInit');


const MODAL_PAGE_PAY = 'pay';
const MODAL_CARD_MONEY_SEND = 'money-send';



class App extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
          activeStory: 'casino',
          history: ["home"],
          scheme: "space_gray", // Если нам не придет тема - ставим эту по умолчанию.
          text: '',
          search: '',
          snackbar: null,
          activeModal: null,
          modalHistory: []
        };
        this.onStoryChange = this.onStoryChange.bind(this);
        this.onChange = this.onChange.bind(this);
        
        
        this.modalBack = () => {
          this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
        };
    }
    onChange (e) { this.setState({ search: e.target.value }); }


    componentDidMount() {
      window.addEventListener('popstate', () => this.goBack());
      bridge.subscribe(({ detail: { type, data }}) => {
        if (type === 'VKWebAppUpdateConfig') { // Получаем тему клиента.
          this.setState({scheme: data.scheme})
        }
      })
    }

    UpdateTheme() {
      if(this.state.scheme === "bright_light" || this.state.scheme === "client_light" ) {
      // Если в скейте такие темы:
          this.setState({scheme: 'space_gray'});
          // Меняем тему на альтернативную
          bridge.send("VKWebAppSetViewSettings", {"status_bar_style": "light", "action_bar_color": "#000"});
          // Изменяем цвет статус-бара и экшен-бара
      } else if(this.state.scheme === "space_gray" || this.state.scheme === "client_dark"){
          this.setState({scheme: 'bright_light'}); 
          // Меняем тему на альтернативную.
          bridge.send("VKWebAppSetViewSettings", {"status_bar_style": "dark", "action_bar_color": "#fff"}); 
          // Изменяем цвет статус-бара и экшен-бара
        }
   }
    
     onStoryChange (e) {
      this.setState({ activeStory: e.currentTarget.dataset.story })
    }
    setActiveModal(activeModal) {
      activeModal = activeModal || null;
      let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];
  
      if (activeModal === null) {
        modalHistory = [];
      } else if (modalHistory.indexOf(activeModal) !== -1) {
        modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
      } else {
        modalHistory.push(activeModal);
      }
  
      this.setState({
        activeModal,
        modalHistory
      });
    };
    
    goBack = () => {
      let history = this.state.history;
      if(history.length === 1) {  // Если в массиве одно значение:
        bridge.send("VKWebAppClose", {"status": "success"}); // отправляем bridge на закрытие сервиса.
      } else if (history.length > 1) {  // Если в массиве больше одного значения:
        history.pop() // Удаляем последний элемент в массиве.
        this.setState({activeStory: history[history.length - 1]}) //Изменяем массив с иторией и меняем активную панель.
      }
    }

    render () {
      const modal = (
        <ModalRoot
        activeModal={this.state.activeModal}
        onClose={this.modalBack}
      >
<ModalPage
          id={MODAL_PAGE_PAY}
          header={
            <ModalPageHeader
            >
              Способ оплаты
            </ModalPageHeader>
          }
          settlingHeight={80}
        >
<Div>
        <Button stretched before={<img src={qiwilogo}/>} size="l" className="qiwibtn"
        onClick={this.onStoryChange}
        selected={this.state.activeStory === 'qiwi'}
        data-story="qiwi"
        >
          Сайт QIWI
        </Button>
      </Div>
      <br/>
      <Div>
        <Button stretched before={<img src={qiwilogo}/>} size="l" className="qiwibtn"
         onClick={() => this.setActiveModal(MODAL_CARD_MONEY_SEND)}
        >
          По номеру QIWI
        </Button>
      </Div>
        </ModalPage>

        <ModalCard
          id={MODAL_CARD_MONEY_SEND}
          onClose={() => this.setActiveModal(null)}
          icon={<img src={qiwi}/>}
          header="+7 922 562-20-21"
          caption="Номер для создания перевода на кошелёк."
          actions={[{
            title: 'Закрыть',
            mode: 'primary',
            action: () => {
              this.setActiveModal(null);
            }
          }]}
        >

        </ModalCard>

      </ModalRoot>
      );
      return (
        <Epic activeStory={this.state.activeStory} tabbar={
          <Tabbar>
            <TabbarItem
              onClick={this.onStoryChange}
              selected={this.state.activeStory === 'still'}
              data-story="still"
              text="Дополнительно"
            ><Icon28SettingsOutline /></TabbarItem>
            <TabbarItem
              onClick={this.onStoryChange}
              selected={this.state.activeStory === 'casino'}
              data-story="casino"
              text="Казино"
            ><img src={mainmenu}/></TabbarItem>
            <TabbarItem
              onClick={this.onStoryChange}
              selected={this.state.activeStory === 'profile'}
              data-story="profile"
              text="Игры"
            ><Icon28UserOutline /></TabbarItem>
          </Tabbar>
        }>
          <View id="qiwi" activePanel="qiwi">
            <Panel id="qiwi">
              <PanelHeader>QIWI.COM </PanelHeader>
              <iframe src="https://qiwi.com/n/DWCASINO" align="center" width="100%" height="100%" frameborder="0" allowfullscreen></iframe>
            </Panel>
          </View>
          <View id="still" activePanel="still"> 
            <Panel id="still">
              <Placeholder
          >
            <Title level="1" weight="heavy" style={{ marginBottom: 20 }}>Правила</Title>
          </Placeholder>
              <Div style={{ color: 'gray' }}>
            Казино Dark Web (Alexia) - в данном казино вы можете делать ставки и выигрывать реальные деньги. Платежи проходят через платёжную систему QIWI. 
            <br/>
            <br/>
            Минимальная сумма ставки составляет 10 рублей. Максимальная сумма 100. Комиссия, которое казино забирает с выигрыша себе 10%. Также в игре принимает участие и само казино, как правило, казино находится под цифрой 1.
            <br/>
            <br/>
            При ставке 20 рублей, вы занимаете 2 числа, при ставке 30 рублей - 3 числа и так далее, сами выбрать число вы не можете.
            <br/>
            Побеждает тот - кто поставит больше.
            <br/>
            <br/>
            Сделано при поддержке <Link href="https://vk.com/darkwebex" target="_blank">DarkWeb</Link>
                      </Div>
            </Panel>
          </View>
          <View id="casino" activePanel="casino" modal={modal} >
            <Panel id="casino">
              <PanelHeader>Казино</PanelHeader>
              <Placeholder
            icon={<img src={maindw}/>}
            header="Казино Alexia"
            action={<Button className="dwbtn" size="l" href="https://vk.com/darkwebex" target="_blank">Перейти в группу</Button>}
          >
          </Placeholder>
          <Card size="l" mode="outline">
          <Placeholder
            icon={<img src={coinmain}/>}
            header="Объявление приза в 20:00 по МСК"
            action={<Button size="xl" mode="outline" onClick={() => this.setActiveModal(MODAL_PAGE_PAY)}>Сделать ставку</Button>}
          >
            Прими участвие, сделай ставку и получи возможность выиграть все деньги.
          </Placeholder>
          </Card>
            </Panel>
          </View>
          <View id="profile" activePanel="profile">
            <Panel id="profile">
              <PanelHeader>Игры</PanelHeader>
              
              <Placeholder
               icon={<img src={game}/>}
          >
            <Title level="1" weight="heavy" style={{ marginBottom: 20 }}>#ПОБЕДИТЕЛИ</Title>
          </Placeholder>
          <CardGrid>
          <Card size="l">
            <div style={{ height: 650 }} />
          </Card>
        </CardGrid>
        <CardGrid>
          <Card size="l" mode="outline">
        <Placeholder
          >
            <Caption level="2" weight="heavy" style={{ marginBottom: 16 }}>Обновление информации происходит вручную, если вас нет в списках, то не волнуйтесь, главное - выплата, а рейтинг потом.</Caption>
          </Placeholder>
          </Card>
        </CardGrid>
            </Panel>
          </View>
        </Epic>
      )
    }
  }
    export default App;